import axios from 'axios';
import config from '../../config';

const punto24Api = axios.create({
    baseURL: config.API_URL
});

// Todo: configurar interceptores
punto24Api.interceptors.request.use( config => {

    config.headers = {
        ...config.headers,
        'x-token': `${localStorage.getItem('token')}`
    }

    return config;
})


export default punto24Api;
