import React, { useEffect, useState } from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { Table } from '../components/Table';
import { getPlacaListOrOne } from '../helpers/api/apiGetList';
// import { data } from '../helpers/data';
import { useAuthStore } from '../hooks/useAuthStore';
import { Login } from '../pages/auth/Login';
import { Register } from '../pages/auth/Register';
import { CreateForm } from '../pages/form/CreateForm';
import { EditarPlaca } from '../pages/Home/EditarPlaca';
import { Index } from '../pages/Home/Index';
import { VerPlaca } from '../pages/Home/VerPlaca';
import { Search } from '../pages/search/Index';
import PrivateRoute from './PrivateRoute';
import PublicRoute from './PublicRoute';




export const MainRoutes = () => {

  const { loading, checkAuthToken, user }= useAuthStore();
  const [data, setData] = useState([]);

  const getDataInf = async () => {
    const newData = await getPlacaListOrOne(false, user.email );
    setData(newData);
  }

  console.log('hola')

  useEffect(() => {
    getDataInf();
    // eslint-disable-next-line
  }, [user])
  

  useEffect(() => {
    checkAuthToken();
    // eslint-disable-next-line
  }, []);

  if (loading) {
    return (
      <h3>Cargango...</h3>
    )
  }
  
  return (
    <BrowserRouter>
        <Routes>
            {/* <Route path='/' element={ <PrivateRoute children={<Index children={<Table data={data} />} />} /> }  /> */}
            <Route path='/' element={ <Index children={<Table data={data} />} /> }  />
            <Route path='/detail/:id' element={ <PrivateRoute children={<Index children={<VerPlaca />} />} /> }  />
            <Route path='/edit/:id' element={ <PrivateRoute children={<Index children={<EditarPlaca />} />} /> }  />
            <Route path='/form/create' element={<PrivateRoute children={<Index children={<CreateForm/>} /> } />}  />
            <Route path='/auth/register' element={ <PrivateRoute children={<Index children={<Register />} />} />} />

            <Route path='/auth/login' element={ <PublicRoute children={<Login />} /> }  />
            <Route path='/search/:id' element={ <Search />}  />

        </Routes>
    </BrowserRouter>
  )
}
