import { useDispatch, useSelector } from "react-redux";
import Swal from 'sweetalert2';
import punto24Api from "../helpers/api/apiCore";
import { checkingCredentials, loginUser, logoutUser, signupUser } from "../redux/auth/authSlice";



export const useAuthStore = () => {

    const { user, userLoggedIn, loading, error } = useSelector((state) => state.Auth);
    const dispatch = useDispatch();

    const startLogin = async (email, password) => {
        dispatch(checkingCredentials());

        try {
            const { data } = await punto24Api.post('/auth', { email, password });
        
            localStorage.setItem('token', data.token);
            localStorage.setItem('token-init-data', new Date().getTime().toString());

            dispatch(loginUser({
                user: data,
                loading: false,
                userLoggedIn: true
            }));

        } catch (error) {
            let { msg } = error.response.data;
            if (!msg) msg = 'Credenciales incorrectas';
            dispatch(logoutUser(msg));
        }
    }

    const createPlaca = async ({
        codigo,
        placa,
        tipovehiculo,
        marca,
        modelo,
        color,
        ano,
        chasis,
        fechaEmision,
        fechaExpiracion,
        razonSocialImportador,
        rncCedulaImportador,
        razonSocialComprador,
        rncCedulaComprador
    }) => {

        dispatch(signupUser({userSignUp: true, error: null }));
        try {

            const { data }= await punto24Api.post('/placa/new', {
                codigo,
                placa,
                tipovehiculo,
                marca,
                modelo,
                color,
                ano,
                chasis,
                fechaEmision,
                fechaExpiracion,
                razonSocialImportador,
                rncCedulaImportador,
                razonSocialComprador,
                rncCedulaComprador,
                creatorPlaca: user.email
            });
            
            dispatch(signupUser({ userSignUp: false, error: null }));
            Swal.fire({
                position: 'center',
                icon: 'success',
                title: `La placa ${data.codigo} fue creado satisfactoriamente`,
                showConfirmButton: false,
                timer: 2500
            })
            .then( () =>{
                window.location = '/';
            })


        } catch (error) {
            let { msg } = error.response.data;
            if (!msg) msg = 'Placa no pudo ser creada.'
            dispatch(signupUser({ userSignUp: false, error: msg }));
            Swal.fire({
                position: 'center',
                icon: 'error',
                title: msg,
                showConfirmButton: false,
                timer: 2500
            });
        }


    }

    const checkAuthToken = async () => {
        const token = localStorage.getItem('token');
        if (!token) return dispatch(logoutUser(null));

        try {
            const { data } = await punto24Api.get('/auth/renew');
            localStorage.setItem('token', data.token);
            localStorage.setItem('token-init-data', new Date().getTime().toString());
            dispatch(loginUser({
                user: data,
                loading: false,
                userLoggedIn: true
            }));
        } catch (error) {
            localStorage.clear();
            dispatch(logoutUser(''));
        }
    }

    

    const startLogout = () => {
        localStorage.clear();
        dispatch(logoutUser(''));
    }

    return {
        //Propiedades
        user,
        userLoggedIn,
        loading,
        error,

        //Metodos
        checkAuthToken,
        startLogin,
        startLogout,
        createPlaca
    }
}
