import React from 'react'
import { useLocation } from 'react-router-dom';
import { Navbar } from '../../components/Navbar';
import PrivateRoute from '../../routes/PrivateRoute';
import {PublicSearch} from '../search/PublicSearch';



const ContainerBody = ({ children }) => {

  return (
    <PrivateRoute>
      <Navbar />
      {children}
    </ PrivateRoute>
  )
}



export const Index = ({ children }) => {

  const url = useLocation();

  const { search } = url;

  const codigo = search.split('=')[1] || null;


  return (
    <>
      {
        (codigo) ?
          <PublicSearch codigo={codigo} /> :
          <ContainerBody children={children} />
      }
    </>
  )
}
