import React from 'react';

export const PlacaProvicional = ({ props }) => {

    const {
        codigo,
        placa,
        tipovehiculo,
        marca,
        modelo,
        color,
        ano,
        chasis,
        fechaEmision,
        fechaExpiracion,
        razonSocialImportador,
        rncCedulaImportador,
        razonSocialComprador,
        rncCedulaComprador
    } = props;

    return (
        <>

            <div id='header'>
                <img src="https://dgii.gov.do/c/images/LogoPlacaProvisional.gif" height="49" alt="Placa Provisional" />
            </div>
            <div id="title">
                <span className="title-orange">Sistema Datamatrix</span> <span className="title-green">Validación
                    de Documentos</span>
            </div>
            <div id="content">
                <table width="100%" border="0" cellPadding="5" cellSpacing="0" className="data_table">
                    <tbody><tr>
                        <td className="left-col">
                            <span id="lblECodigo">Código</span>
                        </td>
                        <td className="right-col">
                            <span id="lblCodigo">{codigo}</span>
                        </td>
                    </tr>
                        <tr>
                            <td className="left-col">
                                <span id="lblEPlaca">Placa</span>
                            </td>
                            <td className="right-col">
                                <span id="lblPlaca">{placa}</span>
                            </td>
                        </tr>
                        <tr>
                            <td className="left-col">
                                <span id="lblETipoVehiculo">Tipo de Vehículo</span>
                            </td>
                            <td className="right-col">
                                <span id="lblTipoVehiculo">{tipovehiculo}</span>
                            </td>
                        </tr>
                        <tr>
                            <td className="left-col">
                                <span id="lblEMarca">Marca</span>
                            </td>
                            <td className="right-col">
                                <span id="lblMarca">{marca}</span>
                            </td>
                        </tr>
                        <tr>
                            <td className="left-col">
                                <span id="lblEModelo">Modelo</span>
                            </td>
                            <td className="right-col">
                                <span id="lblModelo">{modelo}</span>
                            </td>
                        </tr>
                        <tr>
                            <td className="left-col">
                                <span id="lblEColor">Color</span>
                            </td>
                            <td className="right-col">
                                <span id="lblColor">{color}</span>
                            </td>
                        </tr>
                        <tr>
                            <td className="left-col">
                                <span id="lblEYear">Año</span>
                            </td>
                            <td className="right-col">
                                <span id="lblYear">{ano}</span>
                            </td>
                        </tr>
                        <tr>
                            <td className="left-col">
                                <span id="lblEChasis">Chasis</span>
                            </td>
                            <td className="right-col">
                                <span id="lblChasis">{chasis}</span>
                            </td>
                        </tr>
                        <tr id="trfechaemision">
                            <td className="left-col">
                                <span id="LblEfechaemision">Fecha Emisión</span>
                            </td>
                            <td className="right-col">
                                <span id="Lblfechaemision">{fechaEmision}</span>
                            </td>
                        </tr>

                        <tr>
                            <td className="left-col">
                                <span id="LblEFechaexpiracion">Fecha Expiración</span>
                            </td>
                            <td className="right-col">
                                <span id="LblFechaexpiracion">{fechaExpiracion}</span>
                            </td>
                        </tr>
                        <tr id="trNombreImportador">
                            <td className="left-col">
                                <span id="LblENombreImportador">Razón Social / Nombre Importador</span>
                            </td>
                            <td className="right-col">
                                <span id="LblNombreImportador">{razonSocialImportador}</span>
                            </td>
                        </tr>

                        <tr id="trRnCedulaImportador">
                            <td className="left-col">
                                <span id="LblERnCedulaImportador">Rnc/Cédula Importador</span>
                            </td>
                            <td className="right-col">
                                <span id="LblRnCedulaImportador">{rncCedulaImportador}</span>
                            </td>
                        </tr>

                        <tr id="trNombreComprador">
                            <td className="left-col">
                                <span id="LblENombreComprador">Razón Social / Nombre Comprador</span>
                            </td>
                            <td className="right-col">
                                <span id="LblNombreComprador">{razonSocialComprador}</span>
                            </td>
                        </tr>

                        <tr id="trRnCedulaComprador">
                            <td className="left-col">
                                <span id="LblERnCedulaComprador">Rnc/Cédula Comprador</span>
                            </td>
                            <td className="right-col">
                                <span id="LblRnCedulaComprador">{rncCedulaComprador}</span>
                            </td>
                        </tr>

                    </tbody></table>
            </div>
            <div id="footer">
                Direccion General de Impuestos Internos.
            </div>
        </>
    )

}