
import React from 'react';
import { Link } from 'react-router-dom';
import config from '../config';





export const Table = ({ data }) => {

    const handlePrint = async (codigo) => {
        window.location.replace(`${config.API_URL}/placa/pdf/?codigo=${codigo} `);
    }

    return (
        <div className="table-responsive">
            <table className="table">
                <thead>
                    <tr>
                        <th scope="col">Código</th>
                        <th scope="col">Placa</th>
                        <th scope="col">Marca</th>
                        <th scope="col">Modelo</th>
                        <th scope="col">Color</th>
                        <th scope="col">Año</th>
                        <th scope="col">Fecha Emisión</th>
                        <th scope="col">Fecha Expiración</th>
                        <th scope="col">Nombre Comprador</th>
                        <th scope="col">Acción</th>

                    </tr>
                </thead>
                <tbody>

                    {
                        data.map((item) => {
                            return (

                                <tr key={item.codigo}>
                                    <th scope="row">{item.codigo}</th>
                                    <td>{item.placa}</td>
                                    <td>{item.marca}</td>
                                    <td>{item.modelo}</td>
                                    <td>{item.color}</td>
                                    <td>{item.ano}</td>
                                    <td>{item.fechaEmision}</td>
                                    <td>{item.fechaExpiracion}</td>
                                    <td>{item.razonSocialComprador}</td>
                                    <td>
                                        <Link to={`/detail/${item.codigo}`} className='btn btn-outline-info' >
                                            Detalle
                                        </Link>
                                        &nbsp;
                                        <span onClick={ () => handlePrint(item.codigo) } className='btn btn-outline-primary' >
                                            Imprimir
                                        </span>
                                    </td>
                                </tr>
                            )
                        })
                    }


                </tbody>
            </table>

        </div>
    )
}
