import React from 'react';
import { useAuthStore } from '../../hooks/useAuthStore';
import Swal from 'sweetalert2';



export const CreateForm = () => {

    const { createPlaca } = useAuthStore();

    const handleForm = (e) => {
        e.preventDefault();
        const {
            codigo,
            placa,
            tipovehiculo,
            marca,
            modelo,
            color,
            ano,
            chasis,
            fechaEmision,
            fechaExpiracion,
            razonSocialImportador,
            rncCedulaImportador,
            razonSocialComprador,
            rncCedulaComprador
        }
            = e.target;

        if (codigo.value === "" ||
            placa.value ==="" ||
            tipovehiculo.value === "" ||
            marca.value === "" ||
            modelo.value === "" ||
            color.value === "" ||
            ano.value === "" ||
            chasis.value === "" ||
            fechaEmision.value === "" ||
            fechaExpiracion.value === "" ||
            razonSocialImportador.value === "" ||
            rncCedulaImportador.value === "" ||
            razonSocialComprador.value === "" ||
            rncCedulaComprador.value === "") {

            Swal.fire({
                position: 'center',
                icon: 'error',
                title: 'Todos los campos son obligatorio',
                showConfirmButton: false,
                timer: 2500
            });

            return false;

        }

        createPlaca({
            codigo: codigo.value,
            placa: placa.value,
            tipovehiculo: tipovehiculo.value,
            marca: marca.value,
            modelo: modelo.value,
            color: color.value,
            ano: ano.value,
            chasis: chasis.value,
            fechaEmision: fechaEmision.value,
            fechaExpiracion: fechaExpiracion.value,
            razonSocialImportador: razonSocialImportador.value,
            rncCedulaImportador: rncCedulaImportador.value,
            razonSocialComprador: razonSocialComprador.value,
            rncCedulaComprador: rncCedulaComprador.value
        });

    }


    return (
        <div className='card'>

            <div className='card-body'>
                <h5 className="card-title">Registrar Placa</h5>

                <form onSubmit={handleForm} className=''>
                    <div className="row mb-3">
                        <label htmlFor="codigo" className="col-sm-2 col-form-label">Código</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name='codigo' placeholder="Ej: qr1kkss" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="placa" className="col-sm-2 col-form-label">Placa</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name='placa' placeholder="Ej: PP072524" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="tipovehiculo" className="col-sm-2 col-form-label">Tipo de Vehículo</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name='tipovehiculo' placeholder="Ej: AUTOMOVIL PRIVADO" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="marca" className="col-sm-2 col-form-label">Marca</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name='marca' placeholder="Ej: HONDA" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="modelo" className="col-sm-2 col-form-label">Modelo</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name='modelo' placeholder="Ej: Accord" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="color" className="col-sm-2 col-form-label">Color</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name='color' placeholder="Ej: BLANCO" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="ano" className="col-sm-2 col-form-label">Año</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name='ano' placeholder="Ej: 2019" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="chasis" className="col-sm-2 col-form-label">Chasis</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name='chasis' placeholder="Ej: 1HGCV2F34KA026885" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="fechaEmision" className="col-sm-2 col-form-label">Fecha Emisión</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name='fechaEmision' placeholder="Ej: 01/04/2022" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="fechaExpiracion" className="col-sm-2 col-form-label">Fecha Expiración</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name='fechaExpiracion' placeholder="Ej: 30/06/2022" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="razonSocialImportador" className="col-sm-2 col-form-label">Razón Social / Nombre Importador</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name='razonSocialImportador' placeholder="Ej: 2021 CARS SRL" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="rncCedulaImportador" className="col-sm-2 col-form-label">Rnc/Cédula Importador</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name='rncCedulaImportador' placeholder="Ej: 132226933" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="razonSocialComprador" className="col-sm-2 col-form-label">Razón Social / Nombre Comprador</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name='razonSocialComprador' placeholder="Ej: VICTOR MANUEL CALDERON OLIVER" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="rncCedulaComprador" className="col-sm-2 col-form-label">Rnc/Cédula Comprador</label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" name='rncCedulaComprador' placeholder="Ej: 22300974999" />
                        </div>
                    </div>
                    <div className='row mb-3 center'>
                        <button className='btn btn-primary'>
                            Gurardar
                        </button>
                    </div>

                </form>

            </div>

        </div>
    )
}
