import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { getPlacaListOrOne } from '../../helpers/api/apiGetList';
import { PlacaNoFound } from './PlacaNoFound';
import { PlacaProvicional } from './PlacaProvicional';
import './Search.css';




export const Search = () => {

    const { id } = useParams();

    const [data, setData] = useState({});

    const getDataInf = async () => {
        const newData = await getPlacaListOrOne(`${id}`);
        
        setData(newData);
    }

    useEffect(() => {
        getDataInf();
        // eslint-disable-next-line
    }, [])

    return (
        <>
        
            {
                data ? <PlacaProvicional props={data} /> : <PlacaNoFound />
            }
        </>
    )
}
