import punto24Api from "./apiCore";
import Swal from 'sweetalert2';

export const getPlacaListOrOne = async ( codigo, userEmail ) => {
    

    try {
        
        if(!!codigo){
            const { data } = await punto24Api.get('/placa/list', {
                params:{
                    codigo
                }
            });
            
            
            return {...data.placa};
            
        }
        else{

            const { data } = await punto24Api.get('/placa/list',{
                params:{
                    userEmail
                }
            });
            
            return data.placa;
        }

    } catch (error) {
        let { msg } = error.response.data;
        if (!msg) msg = 'Listado no cargo.'
        Swal.fire({
            position: 'center',
            icon: 'error',
            title: msg,
            showConfirmButton: false,
            timer: 2500
        });
        
    }
}

