import { createSlice } from '@reduxjs/toolkit';

const INIT_STATE = {
    user: {
        id: 0,
        firstName: '',
        lastName: '',
        email: '',
        password: '',
        role: '',
        status: '',
        createDate: '',
        token: ''
    },
    loading: false,
    error: false,
    userSignUp: false,
    userLoggedIn: false,
    passwordReset: false,
    passwordChange: false,
    resetPasswordSuccess: null
};

export const authSlice = createSlice({
    name: 'Auth',
    initialState: INIT_STATE,
    reducers: {
        authApiResponseSuccess: () => {

        },
        authApiResponseError: () => {

        },
        loginUser: (state, { payload }) => {
            state.user = payload.user;
            state.loading = payload.loading;
            state.error = false;
            state.userSignUp = false;
            state.userLoggedIn = payload.userLoggedIn;
            state.passwordReset = false;
            state.passwordChange = false;
            state.resetPasswordSuccess = null;
        },
        checkingCredentials: (state) => {
            state.loading = true;
        },
        logoutUser: (state, { payload }) => {
            state.user = {
                id: 0,
                firstName: '',
                lastName: '',
                email: '',
                password: '',
                role: '',
                status: '',
                createDate: '',
                token: ''
            };
            state.loading = false;
            state.error = payload;
            state.userSignUp = false;
            state.userLoggedIn = false;
            state.passwordReset = false;
            state.passwordChange = false;
            state.resetPasswordSuccess = null;
        },
        signupUser: (state, { payload }) => {
            state.userSignUp = payload.userSignUp;
            state.error = payload.error
        },
        forgotPassword: () => {

        },
        resetAuth: () => {

        },

    }
});

export const {
    authApiResponseSuccess,
    authApiResponseError,
    loginUser,
    checkingCredentials,
    logoutUser,
    signupUser,
    forgotPassword,
    resetAuth
} = authSlice.actions;

export default authSlice.reducer;
